import './ResetPassword.scss'

const ResetPassword = () => {
    return (
        <>
            <h4 className="text-light titulo-h4">Nueva contraseña</h4>
            <form id="form-login"><input className="form-control form-control-sm mt-2" id="registro-email" type="email" name="email" placeholder="Email" required="required" />
                <div className="d-flex"><button className="btn btn-1 btn-sm mt-2 mr-2" id="registry">Pedir nueva contraseña</button>
                    <div className="text-success small mt-1" id="cambio-enviado"></div>
                </div>
            </form>
        </>
    )
}

export default ResetPassword;